.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.indexContainer {
  width: 100%;
  height: 100%;
  position: relative;
}

.map,
.tag {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
}
.map .tag {
  z-index: 99;
  position: absolute;
  left: 0;
  top: 0;
  height: 20px;
  width: 20px;
}
.map img {
  height: 200px;
  width: 200px;
}
.loading {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
}
.loadingpng {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  display: block;
}

.loadingText {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.loadinggif {
  padding-right: 20px;
}
.operate {
  text-align: left;
}

.operate h6 {
  padding: 0;
  margin: 0;
}
